
#app {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    height: 95vh;
}

.account {
  padding: 5px;
  border: 1px solid #ddd;
  margin-bottom: 10px;
}
.account .name {
    display: block;
    font-size: 20px;
}
.account .address {
    word-break: break-all;
    background-color: #ffe0f3;
}

.connected {
  text-align: left;
  width: 100%;
  border-spacing: 0;
  margin-bottom: 10px;
  border-spacing: 2px;
  border-color: grey;
  border-collapse: collapse;
}
.connected td, .connected th {
    border: 1px solid #d4d4d4;
    padding: 5px;
}
.info {
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 20px;
}
.network {
  border: 1px solid #ddd;
  padding: 10px;
}

.chat {
  border: 1px solid #ddd;
  padding: 10px;
  margin-top: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.chat .friends {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    width: 200px;
    border-right: 1px solid #ddd;
}
.chat .friends .friends-list {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
.chat .friends .friend {
      cursor: pointer;
      -webkit-transition: 0.3s;
      transition: 0.3s;
      padding: 5px;
}
.chat .friends .friend.selected {
      background-color: pink;
}
.chat .message-text {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    min-height: 50px;
}
.chat .body {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.chat .messages-wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    overflow-y: auto;
}
.chat .title {
    margin: auto;
}
.chat .button {
    padding: 5px;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    cursor: pointer;
}
.chat .button:hover {
    background: #ddd;
}

.message {
  color: #1c1c1c;
  background: #d0d0d0;
  padding: 5px;
  border-radius: 10px;
  margin: 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.message.mine {
  color: #1c1c1c;
  background: #c2f0e1;
  -ms-flex-item-align: end;
      align-self: flex-end;
}
